.app__row {
  clear: both; }

.app__row + * {
  padding-top: 20px; }

.app__text, .app__image {
  display: inline-block; }

.app__text {
  float: right;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px;
  text-align: center; }

.app__image {
  float: left;
  width: 50%; }

.app__row:nth-child(odd) .app__text {
  float: left; }

.app__row:nth-child(odd) .app__image {
  float: right; }



